.todoForm {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.todoList {
  height: 300px;
  width: 500px;
  overflow-y: scroll;
  padding: 10px;
}

.todoList::-webkit-scrollbar {
  width: 5px;
  padding-right: 10px;
}
.todoList::-webkit-scrollbar-track {
  background: rgba(255, 0, 140, 0.215);
}
.todoList::-webkit-scrollbar-thumb {
  background-color: rgba(3, 111, 151, 0.168); /* color of the scroll thumb */
  border-radius: 20px; /* roundness of the scroll thumb */
  border: 3px solid rgba(60, 0, 255, 0.21); /* creates padding around scroll thumb */
}
.refreshBtn {
  border: none;
  background: none;
  position: absolute;
  top: 5px;
  right: 10px;
  fill: #b62828;
  transition: all 1s;
}
.descriptionInput,
input {
  margin: 10px 0 0;
  position: relative;
  resize: none;
  border: 1px solid #965454ad;
  width: 100%;
  outline: none;
  background: linear-gradient(135deg, #e0a78b61 0%, #85b7d1ba 100%);
}
input {
  text-align: center;
}
textarea::placeholder {
  position: absolute;
  top: 35%;
  left: 0;
  right: 0;
  text-align: center;
  color: rgba(16, 16, 40, 0.495);
}
.deleteItem {
  background: none;
  border: none;
  fill: #b62828;
  transition: 0.4s;
  margin: 5px;
}
.itemTitle {
  margin: 5px 10px;
  color: #742312;
}
.deleteItem:hover {
  transform: scale(1.2);
  fill: #911818ba;
}

.descriptionBtn {
  background: none;
  border: none;
  margin: 5px 10px 5px 5px;
  fill: #b62828;
}
.todoListWrp {
  background: linear-gradient(135deg, #e0a78b61 0%, #85b7d1ba 100%);
  border: 2px solid #0000ff30;
  border-radius: 30px 0 0 30px;
  margin: 10px 0;
}
.todoList__item {
  overflow: hidden;
  white-space: normal;
  width: 100%;
  transition: all 1s;
  position: relative;
  display: flex;
}
.activeItem {
  transition: 2s;
  opacity: 1;
  margin: 10px 30px;
  text-align: center;
}
.disabledItem {
  position: absolute;
  right: -300%;
  transition: 1s;
  opacity: 0;
  margin: 10px 30px;
  text-align: center;
}
.todoFormContainer {
  background: linear-gradient(
    315deg,
    rgb(55 159 155 / 32%) 11%,
    #0b060670 100%
  );
  position: relative;
  padding: 30px;
}
.deadline {
  margin: 8px 5px 5px auto;
  font-weight: 600;
}
.addBtn {
  margin: 10px 0 0;
  fill: #b62828;
  background: none;
  border: none;
}
.modalWindow {
  display: flex;
  flex-direction: column;
  align-items: center;
  z-index: 5;
}

.modalbtnWrp {
  width: 100%;
  display: flex;
  justify-content: space-around;
}
.modalButtons {
  margin: 5px;
}
.editBtnWrp {
  display: flex;
  justify-content: space-around;
  flex-direction: row;
  align-items: center;
}
.editBtn {
  margin: 10px 0;
  fill: #3a0d48;
  background: none;
  border: none;
}
