.bgContainer {
  z-index: -1;
  position: absolute;
  display: grid;
  grid-auto-flow: row dense;
  grid-auto-columns: 1fr;
  grid-auto-rows: 1fr;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
  grid-template-areas:
    '. . . . . . . . . '
    '. . . . . . . . . '
    '. . . . . . . . . '
    '. . . . . . . . . '
    '. . . . . . . . . '
    '. . . . . . . . . '
    '. . . . . . . . . '
    '. . . . . . . . . '
    '. . . . . . . . . ';
  min-height: 100vh;
  max-width: 100vw;
  margin: auto auto;
  padding: 0;
  justify-items: center;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: 100%;
  align-content: space-around;
}

.bgChilds {
  width: 100%;
  height: 100%;
  border: 1px solid #631c1c0e;
  animation: change-background ease infinite;
}

@keyframes change-background {
  0% {
    background: #a5070796;
  }

  50% {
    background: #7a500394;
  }
  100% {
    background: #0d021080;
  }
  50% {
    background: #530c0cb5;
  }
  0% {
    background: #b6420691;
  }
}
